.card{
    z-index: 0;
    transition: transform .2s;
    background-color: #1E1E1E;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 18rem;
    width: 20rem;
    margin: 1vh 1vw;
}

.card__image{
    max-width: 5.5rem;
    max-height: 5.5rem;
    border-radius: 50%;
    border: 2px solid white;
}

.card__image-text-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.card__text{
    text-align: left;
    color: white;
    font-weight: 400;
    font-size: 25px;
    margin: 1rem;
}

.card:hover{
    transform: scale(1.05); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
  }