.nav-links {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  list-style: none;
  white-space: nowrap;
  margin-right: 2rem;
  border-style: none;
  cursor: pointer;
}

.nav-links li {
  margin: 2rem 0;
}

.nav-links a {
  border: 1px solid transparent;
  color: black;
  text-decoration: none;
  padding: 1rem 2rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  background: #E7E7E7;
  color: #292929;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 1rem 0 1rem;
  }
}
