.homepage {
  width: 80%;
  margin: 5rem auto;
}

.homepage h1 {
  margin-top: 8rem;
  margin-bottom: 1rem;
  text-align: center;
  color: white;
  font-weight: 600;
  font-size: 70px;
}

.homepage h2 {
  margin-top: 1rem;
  margin-bottom: 2rem;
  text-align: center;
  color: white;
  font-weight: 400;
  font-size: 23px;
}

.homepage p {
  margin-bottom: 7rem;
  text-align: center;
  color: white;
  font-weight: 200;
  font-size: 20px;
  word-spacing: 2px;
}

.aboutus {
  background: rgb(182, 0, 143);
  background: linear-gradient(
    90deg,
    rgba(182, 0, 143, 1) 0%,
    rgba(138, 0, 100, 1) 75%
  );
  width: 100%;
  height: 100%;
  align-items: center;
  padding: 1rem 0 4rem 0;
}

.aboutus h1 {
  text-align: center;
  color: white;
  font-weight: 600;
  font-size: 30px;
  text-transform: uppercase;
}

.aboutus h2 {
  padding: 1rem;
  text-align: center;
  color: white;
  font-weight: 400;
  font-size: 23px;
}

.aboutus__cards {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  height: 20rem;
}

.contact h1 {
  text-align: center;
  color: white;
  font-weight: 600;
  font-size: 30px;
  text-transform: uppercase;
}

.contact h2 {
  text-align: center;
  color: white;
  font-weight: 400;
  font-size: 23px;
}

.reports {
  background: rgb(0, 118, 193);
  background: linear-gradient(
    90deg,
    rgba(0, 118, 193, 1) 0%,
    rgba(0, 118, 168, 1) 75%
  );
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 20rem;
  align-items: center;
  margin: 1rem auto;
  padding-bottom: 5rem;
}

.reports h1 {
  text-align: center;
  color: white;
  font-weight: 600;
  font-size: 30px;
  text-transform: uppercase;
}

.reports h2 {
  text-align: center;
  color: white;
  font-weight: 400;
  font-size: 23px;
}

.project {
  background: rgb(0, 118, 193);
  background: linear-gradient(
    90deg,
    rgba(0, 118, 193, 1) 0%,
    rgba(0, 118, 168, 1) 75%
  );
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  padding-top: 1rem;
  padding-bottom: 5rem;
  align-items: center;
}

.project div {
  width: 70%;
  flex-basis: 0;
  flex-grow: 1;
  align-items: center;
}

.project h1 {
  text-align: center;
  color: white;
  font-weight: 600;
  font-size: 30px;
  text-transform: uppercase;
}

.project h2 {
  text-align: center;
  color: white;
  font-weight: 600;
  font-size: 23px;
  margin-top: 3rem;
  margin-bottom: 1rem;
}

.project p {
  text-align: justify;
  text-justify: inter-word;
  color: white;
  font-weight: 200;
  font-size: 20px;
  word-spacing: 2px;
  margin: 0;
}
