.main-header {
  z-index: 1;
  width: 100%;
  height: 3.2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0 1rem;
  background-color: white;
}

.main-navigation__menu-btn {
  width: 4rem;
  height: 2rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 2rem;
  height: 3px;
  background: black;
}

.main-navigation__desktop-links{
  display: none;
}
.main-navigation__mobile-links{
  display: block;
}

.main-navigation__title{
  color: black;
  letter-spacing: 2px;
  font-weight: 400;
  margin: 0 3rem;
}

.main-navigation__title a, a:visited, a:hover, a:active{
  text-decoration: inherit;
  color: inherit; 
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }
  .main-navigation__header-nav {
    display: block;
  }
  .main-header {
    justify-content: space-between;
  }
  .main-navigation__desktop-links{
      display: block;
  }
}
